}

form {
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
}

input, textarea {
    width: 30%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: #edf1f1;
    resize: none;
    border: 1px solid rgb(57, 55, 55);
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
}

button {
    font-family: 'Noto Serif', serif;
}